body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
body {
  height: 100vh;
  width: 100vw;
}

.login-form {
    border-radius: 4px;
    background-color: white;
}

.nobold {
    font-weight: normal;
}
.sign-up {
    padding-left: 50px;
}
img.bg {
    min-height: 100%;
    min-width: 1024px;
  
    width: 100%;
    height: auto;
  
    position: fixed;
    top: 0;
    left: 0;
    z-index: -1;
  }
  .login {
    background: url(/static/media/background.e2e9e4a0.jpg) no-repeat; 
    background-size: cover;
    min-width: 100vw;
    min-height: 100vh;
  }
  @media screen and (min-width: 768px) {
    .center-login {
        position: absolute;
        z-index: 15;
        top: 50%;
        left: 50%;
        -webkit-transform: translate(-50%, 50%);
                transform: translate(-50%, 50%);
    }
    .center-sign {
        position: relative;
        top: 50%;
        left:50%;
        -webkit-transform: translate(-50%, 50%);
                transform: translate(-50%, 50%);
    }
  }
